// src/utils/PerformanceMonitor.js

import { logger } from "./Logger";

class PerformanceMonitor {
  constructor() {
    this.metrics = {
      navigation: [],
      dataLoading: [],
      chartRendering: [],
      memory: [],
      errors: []
    };
    
    this.observers = new Map();
    this.isMonitoring = false;
    
    this.init();
  }

  init() {
    // 
    this.observePageLoad();
    
    // 
    this.observeNavigation();
    
    // 
    this.observeMemory();
    
    // 
    this.observeErrors();
    
    // 
    this.observeLongTasks();
  }

  start() {
    this.isMonitoring = true;
    logger.debug('');
  }

  stop() {
    this.isMonitoring = false;
    logger.debug('');
  }

  // 
  observePageLoad() {
    if ('performance' in window) {
      window.addEventListener('load', () => {
        const navigation = performance.getEntriesByType('navigation')[0];
        if (navigation) {
          const metrics = {
            timestamp: Date.now(),
            type: 'pageLoad',
            dnsLookup: navigation.domainLookupEnd - navigation.domainLookupStart,
            tcpConnection: navigation.connectEnd - navigation.connectStart,
            requestResponse: navigation.responseEnd - navigation.requestStart,
            domProcessing: navigation.domContentLoadedEventEnd - navigation.domContentLoadedEventStart,
            loadComplete: navigation.loadEventEnd - navigation.loadEventStart,
            totalTime: navigation.loadEventEnd - navigation.navigationStart
          };
          
          this.recordMetric('navigation', metrics);
          this.analyzePerformance(metrics);
        }
      });
    }
  }

  // 
  observeNavigation() {
    if (window.router) {
      const originalPush = window.router.push;
      window.router.push = function(...args) {
        const startTime = performance.now();
        const result = originalPush.apply(this, args);
        
        result.then(() => {
          const endTime = performance.now();
          const metrics = {
            timestamp: Date.now(),
            type: 'navigation',
            from: window.router.currentRoute.value?.path,
            to: args[0],
            duration: endTime - startTime
          };
          
          this.recordMetric('navigation', metrics);
        });
        
        return result;
      }.bind(this);
    }
  }

  // 
  observeMemory() {
    if ('memory' in performance) {
      setInterval(() => {
        const memory = performance.memory;
        const metrics = {
          timestamp: Date.now(),
          usedJSHeapSize: memory.usedJSHeapSize,
          totalJSHeapSize: memory.totalJSHeapSize,
          jsHeapSizeLimit: memory.jsHeapSizeLimit
        };
        
        this.recordMetric('memory', metrics);
        
        // 
        if (memory.usedJSHeapSize > memory.jsHeapSizeLimit * 0.8) {
          logger.warn(':', (memory.usedJSHeapSize / memory.jsHeapSizeLimit * 100).toFixed(2) + '%');
        }
      }, 10000); // 10
    }
  }

  // 
  observeErrors() {
    window.addEventListener('error', (event) => {
      const metrics = {
        timestamp: Date.now(),
        type: 'error',
        message: event.message,
        filename: event.filename,
        lineno: event.lineno,
        colno: event.colno,
        error: event.error?.stack
      };
      
      this.recordMetric('errors', metrics);
    });

    window.addEventListener('unhandledrejection', (event) => {
      const metrics = {
        timestamp: Date.now(),
        type: 'unhandledRejection',
        reason: event.reason
      };
      
      this.recordMetric('errors', metrics);
    });
  }

  // 
  observeLongTasks() {
    if ('PerformanceObserver' in window) {
      try {
        const observer = new PerformanceObserver((list) => {
          for (const entry of list.getEntries()) {
            if (entry.duration > 50) { // 50ms
              const metrics = {
                timestamp: Date.now(),
                type: 'longTask',
                duration: entry.duration,
                startTime: entry.startTime,
                name: entry.name
              };
              
              this.recordMetric('chartRendering', metrics);
              logger.warn(':', entry.duration.toFixed(2) + 'ms');
            }
          }
        });
        
        observer.observe({ entryTypes: ['longtask'] });
      } catch (e) {
        logger.warn(':', e);
      }
    }
  }

  // 
  recordMetric(category, metric) {
    if (!this.isMonitoring) return;
    
    this.metrics[category].push(metric);
    
    // 
    if (this.metrics[category].length > 100) {
      this.metrics[category] = this.metrics[category].slice(-50);
    }
    
    // 
    this.notifyObservers(category, metric);
  }

  // 
  analyzePerformance(metrics) {
    if (metrics.type === 'pageLoad') {
      if (metrics.totalTime > 3000) {
        logger.warn(':', metrics.totalTime.toFixed(2) + 'ms');
      }
      
      if (metrics.dnsLookup > 100) {
        logger.warn('DNS:', metrics.dnsLookup.toFixed(2) + 'ms');
      }
      
      if (metrics.requestResponse > 2000) {
        logger.warn(':', metrics.requestResponse.toFixed(2) + 'ms');
      }
    }
    
    if (metrics.type === 'navigation' && metrics.duration > 1000) {
      logger.warn(':', metrics.duration.toFixed(2) + 'ms');
    }
  }

  // 
  addObserver(category, callback) {
    if (!this.observers.has(category)) {
      this.observers.set(category, []);
    }
    this.observers.get(category).push(callback);
  }

  // 
  removeObserver(category, callback) {
    if (this.observers.has(category)) {
      const callbacks = this.observers.get(category);
      const index = callbacks.indexOf(callback);
      if (index > -1) {
        callbacks.splice(index, 1);
      }
    }
  }

  // 
  notifyObservers(category, metric) {
    if (this.observers.has(category)) {
      this.observers.get(category).forEach(callback => {
        try {
          callback(metric);
        } catch (e) {
          logger.error(':', e);
        }
      });
    }
  }

  // 
  getReport() {
    const report = {
      summary: {},
      details: this.metrics
    };
    
    // 
    Object.keys(this.metrics).forEach(category => {
      const data = this.metrics[category];
      if (data.length > 0) {
        const durations = data.filter(m => m.duration || m.totalTime).map(m => m.duration || m.totalTime);
        if (durations.length > 0) {
          report.summary[category] = {
            count: data.length,
            avgDuration: durations.reduce((a, b) => a + b, 0) / durations.length,
            maxDuration: Math.max(...durations),
            minDuration: Math.min(...durations)
          };
        }
      }
    });
    
    return report;
  }

  // 
  exportData() {
    return JSON.stringify(this.getReport(), null, 2);
  }

  // 
  clearData() {
    Object.keys(this.metrics).forEach(key => {
      this.metrics[key] = [];
    });
    logger.debug('');
  }
}

// 
const performanceMonitor = new PerformanceMonitor();

// 
performanceMonitor.start();

export default performanceMonitor;
export { PerformanceMonitor }; 